import React, { Component } from 'react';

import ImgCom from '../Components/ImageCom'


//varibles
var listOfImages = [];

class Album extends Component {

  constructor(props) {
    super(props);
    this.state = {
      item: props,
      isLoaded: false,
    };
  }



  importAll(r) {
    return r.keys().map(r);
    }
    componentDidMount() {
      listOfImages = this.importAll(require.context('../TestPics/', false, /\.(png|jpe?g|svg)$/));
      this.setState({data:listOfImages});
    }
    render(){
      return(
        <div className="Album">
            {
              /*listOfImages.map(item => console.log(item.default));*/
              listOfImages.map(
                (item) =>    <ImgCom key={item.default} src={item.default} alt="info"></ImgCom>
              )
            }
        </div>
      )
    }
}
 
export default Album;