import './App.css';
import '../src/CSS/Main.css'
import Album from './Components/album'
import Nav from './Components/nav'
import Home from './Components/homePage'

import {BrowserRouter as Router,Switch,Route} from 'react-router-dom';

function App() {
  return (
    <Router>
      <div className="App">
      <Nav></Nav>
      <Switch>
        <Route path='/HaasbroekTroue' exact component={Home}></Route>  
        <Route path='/HaasbroekTroue/Album' component={Album}></Route>
      </Switch>
      
      
    </div>
        
      </Router>
    
  );
}

export default App;
