import React, { Component } from 'react'
import {Link} from 'react-router-dom';

const tempStyle = {
    color:'white',
}


export class NavBar extends Component {

    render() {
        return (
            <nav>
                <Link style={tempStyle} to='/HaasbroekTroue'>
                    <h3>Logo/Home</h3>
                </Link>

                <ul className="navLinks">
                    <Link style={tempStyle} to='/HaasbroekTroue/Album'>
                        <li>Album</li>
                    </Link>
                </ul>
            </nav>
        )
    }
}

export default NavBar
