import React, { Component } from 'react'
import Loader from '../Components/loader'
export default class ImageCom extends Component {

    constructor(props) {
        super(props);
        this.state = {
          item: props,
          isLoaded: false,
        };
      }

    componentDidMount() {
        this.setState({
            loaded: false,
            error: false
            });
        const img = new Image();
        img.onload = () => {
            this.setState({
            loaded: true
            });
        };
        img.onerror = () => {
            this.setState({
            error: true
            });
        };
        img.src = this.props.src;
    }

    generateElement(){
      return (
      <div className="flex-container">
        <img src={this.props.src} alt="An Image"/>
        <a href={this.props.src} download={this.props.src}> Download </a>
      </div>
      );
    
    }

    render() {
        if (this.state.error) {
          return this.generateElement();
        } else if (!this.state.loaded) {
          return <div><Loader className="center"></Loader></div>
        }
        return this.generateElement();
      }
}
